import * as React from 'react';
import styled from 'styled-components';

const FooterEl = styled.div`
  width: 100%;
  /* height: 10vh; */
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000110;
  color: white;
  & > h1 {
    font-family: 'Merriweather', serif;
  }
  &.stick {
    position: fixed;
    bottom: 0;
    left: 0;
  }
`;

class Footer extends React.Component {
  constructor(props) {
    super(props);

    this.wrapperRef = React.createRef();

    this.state = {
      stickToBottom: false
    }
  }

  componentDidMount () {
    const wrapper = this.wrapperRef.current;
    if (!wrapper) {
      return;
    }
    const { bottom } = wrapper.getBoundingClientRect();
    const { innerHeight } = window;
    if (bottom < innerHeight) {
      this.setState({ stickToBottom: true });
    }
  }

  render () {
    const { stickToBottom } = this.state;
    return (
      <FooterEl ref={ this.wrapperRef } className={ stickToBottom && 'stick' || '' }>
        <h1>МК, { new Date().getFullYear() } ©</h1>
      </FooterEl>
    );
  }
};

export default Footer;